<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="{'active': headerActive}" left-arrow>
      <template #left v-if="Headtype === 'persale'">
        <img src="@/assets/images/record.png" @click="$router.push({name: 'Index'})" alt="">
      </template>
      <template #left v-else-if="Headtype === 'back'">
        <img class="back" src="@/assets/images/back.png" @click="$goBack" alt="">
      </template>
      <template #left v-else>
        <img src="@/assets/images/more.png" @click="onMenu" alt="">
      </template>
      <template #right v-if="Headtype !== 'back'">
        <div class="wallet-block flex-center">
          <!-- <div> -->
          <img class="wallet-img" src="@/assets/images/wallet.png" alt="">
          <!-- </div> -->
          <!-- {{address}} -->
          <span v-if="address" class="address flex-center">***{{address.substring(address.length-3, address.length)}}</span>
          <span v-else class="address flex" @click="initWallet">{{$t('web3.连接钱包')}}</span>
        </div>
      </template>
    </van-nav-bar>
    <!-- 菜单栏 -->
    <div class="menu-Block" v-show="open" @click.self="closeMenu()">
      <div class="menu-leftBlock">
        <div class="menu-item flex-middle" @click="openUrl('https://exchange.pancakeswap.finance/#/swap?inputCurrency=0x55d398326f99059fF775485246999027B3197955&outputCurrency=0xe97364f13Fc8f40B0ab372a51b645a648f091483')">
          <img class="menu-img" src="@/assets/images/buy.png">
          <div class="menu-tit">{{$t('index.购买ART')}}</div>
        </div>
        <div class="menu-item flex-middle" @click="$router.push({'name': 'Receive'})">
          <img class="menu-img" src="@/assets/images/receive.png">
          <div class="menu-tit">{{$t('index.领取ART奖励')}}</div>
        </div>
        <div class="menu-item flex-middle">
          <img class="menu-img" src="@/assets/images/gitub.png" @click="openUrl('https://github.com/bbjs123/artContract')">
          <div class="menu-tit">{{$t('index.GitHub')}}</div>
        </div>
        <a class="menu-item flex-middle" v-if="local === 'zh-cn'" href="/ARTChainSmartContractSecurityAuditReport-Chineseversion.pdf">
          <img class="menu-img" src="@/assets/images/report.png">
          <a class="menu-tit">{{$t('index.审计报告')}}</a>
        </a>
        <a class="menu-item flex-middle" v-else href="/ARTChainSmartContractSecurityAuditReport.pdf">
          <img class="menu-img" src="@/assets/images/report.png">
          <div class="menu-tit">{{$t('index.审计报告')}}</div>
        </a>
        <!-- <div class="menu-item flex-middle">
          <img class="menu-img" src="@/assets/images/white-paper.png">
          <a class="menu-tit" href="/ArtChainWhitepaper.pdf">{{$t('index.白皮书')}}</a>
        </div> -->
        <a class="menu-item flex-middle" href="/ArtChainWhitepaper.pdf">
          <img class="menu-img" src="@/assets/images/white-paper.png">
          <div class="menu-tit" >{{$t('index.白皮书')}}</div>
        </a>
        <div class="menu-item flex-middle" @click="openUrl('https://0.plus/ARTChainNFT1')">
          <img class="menu-img" src="@/assets/images/telegraph.png">
          <div class="menu-tit">{{$t('index.电报')}}</div>
        </div>
        <div class="menu-item flex-middle" @click="openUrl('https://twitter.com/ARTChainNFT')">
          <img class="menu-img" src="@/assets/images/twitter.png">
          <div class="menu-tit">{{$t('index.推特')}}</div>
        </div>
        <div class="menu-item flex-middle">
          <img class="menu-img" src="@/assets/images/en.png">
          <!-- <div class="menu-tit">{{$t('index.Engish')}}</div> -->
          <div class="menu-tit" v-if="local === 'en'" @click="changeLanuage('zh-cn')">English</div>
          <div class="menu-tit" v-else @click="changeLanuage('en')">中文</div>
        </div>
        <div class="menu-item flex-middle" @click="openUrl('https://bscscan.com/address/0xe97364f13Fc8f40B0ab372a51b645a648f091483')">
          <img class="menu-img" src="@/assets/images/source.png">
          <div class="menu-tit">{{$t('index.开源')}}</div>
        </div>
        <!-- <div class="menu-item flex-middle" @click="$router.push({name:'Share'})">
          <img class="menu-img" src="@/assets/images/telegraph.png">
          <div class="menu-tit">{{$t('index.邀请')}}</div>
        </div> -->
      </div>
    </div>
  </div>
  <Footer></Footer>
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
import Footer from "@/components/Footer";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
import pdf from 'vue-pdf'
export default {
  components: {
    Footer
  },
  props: {
    Headtype: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
  },
  setup(props) {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      Headtype: props.Headtype,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "zh-cn",
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      initContract: () => {
        data.initData()
      },
      initData: () => {
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      onMenu: () => {
        data.open = !data.open
      },
      openUrl: (url) => {
        window.location.href = url;
      },
      closeMenu: () => {
        data.open = false
      }
    })
    document.addEventListener('scroll', function () {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
  .active{
    background: #0066EE !important;
  }
  .wallet-block{
    border: 1px solid #FFFFFF;
    border-radius: 20px;
    padding: 4px 10px;
    width: 108px;
    .wallet-img{
      width: 19px;
      height: 19px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .address{
      overflow: hidden;
      white-space:nowrap;
      font-size: 14px;
      color: #FFFFFF;
      margin-left: 10px;
    }
  }
  .menu-Block{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    z-index: 600;
    .menu-leftBlock{
      position: fixed;
      left: 0;
      top: 0;
      bottom: 0;
      background: #fff;
      right: 40%;
      .menu-item{
        padding: 14px 20px;
        .menu-img{
          width: 22px;
          height: 22px;
        }
        .menu-tit{
          font-size: 14px;
          color: #222222;
          margin-left: 20px;
        }
      }
    }
  }
  .back{
    width: 10px !important;
    height: 18px;
  }
</style>
