<template>
  <div class="footer">
    <van-tabbar v-model="active" route>
      <van-tabbar-item to="/">
        <span>{{$t('index.首页')}}</span>
        <template #icon="props">
          <img :src="props.active ? icon.active : icon.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="" @click="ending">
        <span>{{$t('index.预售')}}</span>
        <template #icon="props">
          <img :src="props.active ? icon.active1 : icon.inactive1" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/receive">
        <span>{{$t('index.领取ART')}}</span>
        <template #icon="props">
          <img :src="props.active ? icon.active2 : icon.inactive2" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/LockPool">
        <span>{{$t('index.锁池')}}</span>
        <template #icon="props">
          <img :src="props.active ? icon.active3 : icon.inactive3" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/Application">
        <span>{{$t('index.应用')}}</span>
        <template #icon="props">
          <img :src="props.active ? icon.active4 : icon.inactive4" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { reactive, toRefs, ref } from "vue";
import { Toast } from "vant";
import { useI18n } from "vue-i18n";
export default {
  setup() {
    const { t } = useI18n()
    const active = ref(0);
    const data = reactive({
      $t: t,
      ending: () => {
        Toast('预售已结束')
      }
    })
    const icon = {
      active: require("@/assets/images/nav1-a.png"),
      inactive: require("@/assets/images/nav1.png"),
      active1: require("@/assets/images/nav2-a.png"),
      inactive1: require("@/assets/images/nav2.png"),
      active2: require("@/assets/images/nav3-a.png"),
      inactive2: require("@/assets/images/nav3.png"),
      active3: require("@/assets/images/nav4-a.png"),
      inactive3: require("@/assets/images/nav4.png"),
      active4: require("@/assets/images/nav5-a.png"),
      inactive4: require("@/assets/images/nav5.png"),
    };
    const refData = toRefs(data);
    return {
      ...refData, icon, active
    };
  }
}
</script>
